.toolbar {
    top: 2rem;
    left: 0;
    width: 100%;
    background: none;
    font-size: 1.1rem;
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 2rem 5rem 2rem 3rem;
}

.toolbar__logo {
    margin-left: 1rem;
}

.toolbar__logo a {
    color: gray;
    text-decoration: none;
    font-size: 1.5rem; 
}

.spacer {
    flex: 1;
}

.toolbar_navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar_navigation-items li {
    padding: 0 0.8rem;
}

.toolbar_navigation-items a {
    color: grey;
    text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
    color: #164D71;
}

@media (max-width: 768px) {
    .toolbar_navigation-items {
        display: none;
    }
}

@media (min-width: 769px) {
    .toolbar__toggle-button {
        display: none;
    }

    .toolbar__logo {
        margin-left: 0; 
    }
}